import React, { Component, Suspense } from 'react';
import { observable, action, toJS, computed } from 'mobx';
import _ from 'lodash';
import { useTranslation, withTranslation, Trans } from 'react-i18next';
import { Collapse } from 'antd';
import hoistStatics from 'hoist-non-react-statics';
import {Koffing} from 'koffing';
import teamsData from './../assets/teams/gen7/teams.json';
import Handlebars from "handlebars";
const { Panel } = Collapse;

class Teams extends Component {
  get t(){ return this.props.t; }
  get i18n(){ return this.props.i18n; }

  render(){

    const teamCode = `
    === [gen7] VGC17/Gengar fun ===

    O(n.n)O/ (Gengar) (F) @ Gengarite
IVs: 0 Atk
EVs: 252 HP / 44 Def / 4 SpA / 28 SpD / 180 Spe
Ability: Cursed Body
Level: 50
Shiny: Yes
Timid Nature
- Protect
- Skill Swap
- Will-O-Wisp
- Hex

Kangaskhan (F) @ Kangaskhanite
EVs: 212 HP / 138 Atk / 60 Def / 100 Spe
Ability: Scrappy
Level: 50
Adamant Nature
- Protect
- Power-Up Punch
- Return
- Sucker Punch

Reisen (Azumarill) (F) @ Sitrus Berry
IVs: 26 HP / 15 SpA
EVs: 252 HP / 252 Atk / 6 SpD
Ability: Huge Power
Level: 50
Shiny: Yes
Adamant Nature
- Belly Drum
- Protect
- Aqua Jet
- Play Rough

火神蛾 (Volcarona) (F) @ Leftovers
EVs: 6 HP / 252 SpA / 252 Spe
Ability: Flame Body
Shiny: Yes
Modest Nature
- Protect
- Quiver Dance
- Fiery Dance
- Bug Buzz

GeoCat (Landorus-Therian) (M) @ Groundium Z
EVs: 68 HP / 252 Atk / 188 Spe
Ability: Intimidate
Jolly Nature
- Protect
- Earthquake
- Explosion
- U-turn

甲贺忍蛙 (Greninja) (M) @ King’s Rock
EVs: 6 HP / 252 Atk / 252 Spe
Ability: Protean
Level: 50
Shiny: Yes
Jolly Nature
- Mat Block
- Fling
- Acrobatics
- U-turn
`;
    // console.log(Koffing.toJson(teamCode));

    const EVs = {
      'hp':'HP',
      'atk':'Atk',
      'def':'Def',
      'spa':'SpA',
      'spd':'SpD',
      'spe':'Spe'
    };

    Handlebars.registerHelper('ifGender', function(v1, v2) {
      return v1?'('+v1+')':'';
    });
    Handlebars.registerHelper('ifEVs', function(v1, v2) {
      return v1?'EVs:':'';
    });
    Handlebars.registerHelper('ifIVs', function(v1, v2) {
      return v1?'IVs:':'';
    });
    Handlebars.registerHelper('withEVIVs', function(v1, v2, v3) {
      return v2=='0'?' '+ v3 + ' ' + EVs[v1]:'/ '+ v3 + ' ' + EVs[v1];
    });
    
    const hbr = `
    <ul class="pokemon_list">
      {{#each pokemon}}
        <div>
          <p>{{this.name}} 
          {{ifGender this.gender}}
           @ {{this.item}}</P>
          <p>Ability: {{this.ability}}</P>
          <p>{{this.nature}} Nature</P>

          <p>{{ifEVs this.evs}}
            {{#each evs}}
              {{withEVIVs @key @index this}}
            {{/each}}
          </P>

          <p>{{ifIVs this.ivs}}
            {{#each ivs}}
              {{withEVIVs @key @index this}}
            {{/each}}
          </P>
                
          {{#each moves}}
            <p>- {{this}}</P>
          {{/each}}
          <br/>
        </div>
      {{/each}}
    </ul>
    `;
    
    const template = Handlebars.compile(hbr);

    return (
      <div>
        <h3>{this.t('gen7')}</h3>
        <div>
          <Collapse>
            {_.map(teamsData.teams, x =>{
              return(
                <Panel header={ x.folder + ' ' + x.name} key={x.name}>
                  <div dangerouslySetInnerHTML={{ __html: template(x) }} style={{lineHeight:'50%'}} />
                </Panel>
              );
            })}
          </Collapse>
        </div>
        
        
        
      </div>
    );  
  }
};

export default hoistStatics(withTranslation()(Teams), Teams);
